@charset "UTF-8";
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  width: 100%;
  margin: 0px;
}

.cabecera {
  background-color: rgb(27, 37, 128);
  width: 100%;
  position: sticky;
  padding: 10px;
}
.cabecera__menu-navegacion-nav__lista-cabecera {
  font-size: 14px;
  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}
.cabecera__menu-navegacion-nav__lista-cabecera__menu-hamburguesa {
  display: flex;
  flex-flow: row wrap;
  align-items: center;
  justify-content: space-between;
  width: 79%;
}
.cabecera__menu-navegacion-nav__lista-cabecera__menu-hamburguesa__li {
  list-style-type: none;
  font-family: "Staatliches";
}
.cabecera__menu-navegacion-nav__lista-cabecera__menu-hamburguesa__enlaceRegistro {
  width: 50px;
  border-radius: 0.5em;
  padding: 6px;
  color: rgb(27, 37, 128);
  background-color: rgb(204, 166, 31);
}
.cabecera__menu-navegacion-nav__lista-cabecera__nav-label, .cabecera__menu-navegacion-nav__lista-cabecera__nav-input {
  /* Aqu칤 utilizamos el display none para ocultar el men칰
  hamburguesa cuando se encuentra en vista ordenador */
  display: none;
}
.cabecera__menu-navegacion-nav__lista-cabecera .nav-label__icono-hamburguesa {
  width: 30px;
}
.cabecera__menu-navegacion-nav__lista-cabecera__contenedor-logo-y-nombre {
  color: white;
  font-family: "Staatliches";
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 3px;
}
.cabecera__menu-navegacion-nav__lista-cabecera__contenedor-logo-y-nombre__logo-pagina {
  width: 45px;
}
.cabecera__menu-navegacion-nav__lista-cabecera__contenedor-logo-y-nombre__nombre-pagina {
  color: white;
  /* Aqu칤 utilizamos un display inline-block para
  que el nombre de Coinbase se coloque justo al lado
  del logo de la p치gina y no debajo */
  display: inline-block;
}

@media screen and (min-width: 378px) and (max-width: 520px) {
  .cabecera__menu-navegacion-nav__lista-cabecera__nav-label {
    display: block;
    /* Aqu칤 especificamos que se ponga el puntero seleccionando
    la imagen del men칰 hamburguesa cuando pasamos por encima  */
    cursor: pointer;
  }
  .cabecera__menu-navegacion-nav__lista-cabecera__menu-hamburguesa {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    /* Con el column indicamos que cada elemento de la lista
    se pone uno debajo de otro */
    flex-flow: column wrap;
    background-color: rgb(204, 166, 31);
    right: 5px;
    top: 14px;
    width: 160px;
    height: 330px;
    padding-top: 20px;
    position: absolute;
    float: right;
    z-index: 1;
    /* Con clip-path indicamos que se oculte
    el men칰 mientras no pulsemos el bot칩n
    y despu칠s indicamos que la
    transici칩n dura 1 segundo y que se produzca a la entrada
    y a la salida */
    clip-path: circle(0 at center);
    transition: clip-path 1s ease-in-out;
  }
  .cabecera__menu-navegacion-nav__lista-cabecera__menu-hamburguesa__li__a {
    color: black;
  }
  .cabecera__menu-navegacion-nav__lista-cabecera__menu-hamburguesa__enlaceRegistro {
    color: rgb(0, 0, 0);
    background-color: rgb(204, 166, 31);
    border-radius: 0;
    padding: 0;
  }
  .cabecera__menu-navegacion-nav__lista-cabecera__icono-hamburguesa {
    display: flex;
    justify-content: center;
    align-items: center;
    top: 15;
    right: 5;
    padding: 5px;
    cursor: pointer;
    background-color: rgb(204, 166, 31);
    position: absolute;
    /* Con el z-index hacemos que el icono de hamburguesa
    se posicione por encima del men칰 hamburguesa */
    z-index: 2;
  }
  .cabecera__menu-navegacion-nav__nav-input:checked + .cabecera__menu-navegacion-nav__menu-hamburguesa {
    /* Indicamos que aparezca el men칰 y que vaya cerr치ndose
    desde el 100% de tama침o hacia el centro
    y que se abra desde el centro hasta el 100% de tama침o cuando
    pulsamos el input de tipo checkbox */
    clip-path: circle(100% at center);
  }
}
@media screen and (max-width: 378px) {
  .cabecera__menu-navegacion-nav__lista-cabecera {
    position: relative;
  }
  .cabecera__menu-navegacion-nav__lista-cabecera__nav-label {
    display: block;
    cursor: pointer;
  }
  .cabecera__menu-navegacion-nav__lista-cabecera__menu-hamburguesa {
    flex-flow: column wrap;
    background-color: rgb(204, 166, 31);
    top: 5;
    right: 0;
    width: 130px;
    height: 290px;
    padding-top: 40px;
    padding-bottom: 25px;
    position: absolute;
    z-index: 1;
    clip-path: circle(0 at center);
    transition: clip-path 1s ease-in-out;
  }
  .cabecera__menu-navegacion-nav__lista-cabecera__menu-hamburguesa__li__a {
    color: black;
  }
  .cabecera__menu-navegacion-nav__lista-cabecera__menu-hamburguesa__enlaceRegistro {
    color: rgb(0, 0, 0);
    background-color: rgb(204, 166, 31);
    border-radius: 0;
    padding: 0;
  }
  .cabecera__menu-navegacion-nav__lista-cabecera__icono-hamburguesa {
    display: flex;
    top: 5;
    right: 0;
    padding: 5px;
    cursor: pointer;
    background-color: rgb(204, 166, 31);
    position: absolute;
    z-index: 2;
  }
  .cabecera__menu-navegacion-nav__nav-input:checked + .cabecera__menu-navegacion-nav__menu-hamburguesa {
    /*  La propiedad clip-path en este caso lo que hace es indicar
    que cuando se pulse el input checkbox aparezca el men칰
    hamburguesa */
    clip-path: circle(100% at center);
  }
}
.pie-pagina {
  background-color: rgb(27, 37, 128);
  position: sticky;
  width: 100%;
  height: 55px;
  padding: 10px;
  display: flex;
  /* La propiedad flex-direction row lo que hace
  es indicar que los elementos se coloquen
  uno detr치s de otro en fila */
  flex-flow: row wrap;
  /* La propiedad justify-content space-between distribuye
  los elementos de manera uniforme
  El primer elemento est치 al ras con el inicio,
  el 칰ltimo est치 al ras con el final   */
  justify-content: space-between;
  align-items: center;
}
.pie-pagina__contenedor-redes-sociales {
  /* La propiedad justify-self lo que hace es mover un 칰nico
  elemento sobre el eje horizontal. En este caso coloca
  al principio del eje horizontal el elemento   */
  justify-self: flex-start;
}
.pie-pagina__contenedor-redes-sociales__icono-insta {
  width: 18px;
}
.pie-pagina__contenedor-redes-sociales__icono-facebook {
  width: 18px;
}
.pie-pagina__contenedor-redes-sociales__enlace-insta {
  justify-self: flex-start;
  color: #ffffff;
  font-family: "Staatliches";
  font-size: 12px;
}
.pie-pagina__contenedor-redes-sociales__enlace-facebook {
  /* La propiedad justify-self lo que hace es mover un 칰nico
  elemento sobre el eje horizontal. En este caso coloca
  el elemento en el centro del eje horizontal   */
  justify-self: center;
  color: #ffffff;
  font-family: "Staatliches";
  font-size: 12px;
}
.pie-pagina__enlace-invierte {
  /* La propiedad justify-self lo que hace es mover un 칰nico
  elemento sobre el eje horizontal. En este caso coloca
  el elemento al final del eje horizontal   */
  justify-self: flex-end;
  color: #ffffff;
  font-family: "Staatliches";
  font-size: 12px;
}
.pie-pagina__enlace-pag-404 {
  color: #ffffff;
  font-family: "Staatliches";
  font-size: 12px;
}
.pie-pagina__enlace-pag-perfil-usuario {
  color: #ffffff;
  font-family: "Staatliches";
  font-size: 12px;
}

.main-pagina-contacto {
  background-color: rgb(105, 120, 255);
  width: 100%;
  /* 100% de la altura del viewport
  El viewport es la parte visible del documento */
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.main-pagina-contacto .section__formulario-contacto {
  text-align: center;
  padding: 30px;
}
.main-pagina-contacto .section__formulario-contacto .formulario-contacto__fieldset .fieldset__titulo-principal {
  color: white;
  font-family: "Inter", sans-serif;
  font-size: 18px;
  text-align: center;
  padding-top: 30px;
}
.main-pagina-contacto .section__formulario-contacto .formulario-contacto__fieldset .fieldset__titulo-principal h2::after {
  /* Con el h2::after lo que hace es decirle que despu칠s
  del t칤tulo ponga como contenido el elemento que se
  indique en el content */
  content: "游녢";
}
.main-pagina-contacto .section__formulario-contacto fieldset {
  background-color: rgb(27, 37, 128);
  border: rgb(204, 166, 31);
  width: 550px;
  padding: 10px;
  padding-bottom: 35px;
}
.main-pagina-contacto .section__formulario-contacto fieldset .fieldset__parrafo-nombre {
  /* La propiedad de display flex, ayuda a colocar
  los elementos como si fueran celdas de tablas, con un
  comportamiento predecible y adaptable. */
  display: flex;
  /* Con el justify-content space-evenly lo que hace
  es indicar que los elementos se separen
  distribuyendo el espacio entre los elementos de manera
  uniforme. Es similar a space-around pero proporciona
  un espacio igual en lugar de la mitad del tama침o en los
  bordes. */
  justify-content: space-evenly;
  /* La propiedad align-items center lo que hace
  es centrar los elementos respecto al eje vertical */
  align-items: center;
  padding: 15px;
  padding-top: 30px;
}
.main-pagina-contacto .section__formulario-contacto fieldset .fieldset__parrafo-correo {
  display: flex;
  justify-content: space-evenly;
  /* La propiedad flex-direction row lo que hace
  es indicar que los elementos se coloquen
  uno detr치s de otro en fila */
  flex-direction: row;
  align-items: center;
  gap: 12px;
  padding: 15px;
}
.main-pagina-contacto .section__formulario-contacto fieldset .fieldset__parrafo-duda {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  gap: 22px;
